


























































































































import { Component, Vue } from 'vue-property-decorator';
import circulationDetails from './components/circulationDetails.vue';
import { obj2FormData } from '@utils/util';
import paramsCache from '@mixins/paramCache';
import {
  queryHistoryTaskList,
  getNodeListById,
  queryNameList,
  queryProcessTagsList
} from '@api/processAssignment';

Component.registerHooks(['beforeRouteLeave']);
const paramsCacheMixin = paramsCache([
  'queryParams',
  'tableDataList',
  'tableTotal',
  'processTagsList',
  'processNameList'
]);
@Component({
  components: { circulationDetails },
  mixins: [paramsCacheMixin]
})
export default class HistoryAssigment extends Vue {
  private processTagsList: Array<any> = [];
  private processNameList: Array<any> = [];
  private queryParams: any = {
    type: '0',
    processName: [],
    processNode: [],
    processTags: [],
    limit: 20,
    page: 1,
    productCode: '',
    productContractAbbreviation: ''
  };

  private tableDataList: Array<any> = [];
  private tableTotal = 0;
  private tableLoading = false;
  private taskMsg: any = {};
  private dialogTitle = '';
  private dialogVisible = false;
  private dialogSelect = '';
  private dialogSelectList: Array<any> = [];
  private dialogSelectContent = '';
  private histroyDialog = false;
  private historyDetail: any = {};
  private taskNameList: Array<any> = [];
  private processInstanceId = '';

  columns() {
    const _this = this;
    return [
      {
        prop: '',
        label: '序号',
        type: 'index',
        width: 60,
        align: 'center'
      },
      {
        prop: 'processName',
        label: '流程名称',
        'header-align': 'left',
        minWidth: 140,
        'show-overflow-tooltip': true
      },
      {
        prop: 'launchingMode',
        label: '发起方式',
        minWidth: 120,
        'header-align': 'left'
      },
      {
        prop: 'productCode',
        label: '产品主代码',
        'header-align': 'left',
        minWidth: 140,
        'show-overflow-tooltip': true
      },
      {
        prop: 'productContractAbbreviation',
        label: '产品合同简称',
        'header-align': 'left',
        minWidth: 140,
        'show-overflow-tooltip': true
      },
      {
        prop: 'launchingTime',
        label: '发起时间',
        'header-align': 'left',
        width: 170
      },
      {
        prop: 'processTags',
        label: '流程标签',
        'header-align': 'left',
        minWidth: 140,
        'show-overflow-tooltip': true
      },
      {
        prop: 'processStatus',
        label: '流程状态',
        minWidth: 140,
        align: 'center',
        render: (h: any, { record }: any) => {
          return h(
            'p',
            {
              style: {
                margin: '0'
              }
            },
            record.processStatus.map((item: any) => {
              return h(
                'el-tag',
                {
                  class: {
                    tag: true,
                    'tag-normal': item === '正常',
                    'tag-will': item === '将要超时',
                    'tag-timeout': item === '超时' || item === '催办'
                  }
                },
                item
              );
            })
          );
        }
      },
      {
        label: '操作',
        minWidth: 100,
        align: 'center',
        fixed: 'right',
        actions: [
          {
            code: 'history',
            text: '流转历史',
            onClick: this.flowHistory
          }
        ]
      }
    ];
  }

  created() {
    !this.tableDataList.length && this.getHistoryTaskList();
    !this.processTagsList.length && this.getProcessTags();
    !this.processNameList.length && this.getProcessName();
  }
  beforeRouteLeave(to, from, next) {
    this.cacheParamData();
    next();
  }
  // 标签点击
  selectTag(tag: any) {
    if (tag === '全部') {
      if (this.queryParams.processTags.length === this.processTagsList.length) {
        this.queryParams.processTags = [];
      } else {
        this.queryParams.processTags = JSON.parse(
          JSON.stringify(this.processTagsList)
        );
      }
    } else {
      const tagIndex = this.queryParams.processTags.indexOf(tag);
      if (tagIndex === -1) {
        this.queryParams.processTags.push(tag);
        if (
          this.queryParams.processTags.length ===
          this.processTagsList.length - 1
        ) {
          this.queryParams.processTags.push('全部');
        }
      } else {
        this.queryParams.processTags.splice(tagIndex, 1);
        const allTag = this.queryParams.processTags.indexOf('全部');
        if (allTag !== -1) {
          this.queryParams.processTags.splice(allTag, 1);
        }
      }
    }
    this.handleSearch();
  }
  // 流程名称切换
  nameChange(e: any) {
    // this.taskNameList = [];
    // this.queryParams.processNode = [];
    // if (e.length === 1) {
    //   getNodeListById({ processDefinitionKeys: e }).then(({ error, data }) => {
    //     if (!error) {
    //       this.taskNameList = data;
    //     }
    //   });
    // }
  }

  // 标签列表查询
  getProcessTags() {
    queryProcessTagsList().then((res) => {
      if (res.data) {
        res.data.unshift('全部');
        this.processTagsList = res.data;
      } else {
        this.$message.error('标签列表查询失败：' + res.error.message);
      }
    });
  }
  // 模板名称查询
  getProcessName() {
    queryNameList().then((res) => {
      if (res.data) {
        this.processNameList = res.data;
      } else {
        this.$message.error('标签列表查询失败：' + res.error.message);
      }
    });
  }
  // 任务跟踪列表查询
  getHistoryTaskList() {
    const params = {
      templateIds: this.queryParams.processName,
      processTags: JSON.parse(JSON.stringify(this.queryParams.processTags)),
      // nodeIds: this.queryParams.processNode,
      trackType: this.queryParams.type,
      limit: this.queryParams.limit,
      page: this.queryParams.page,
      productCode: this.queryParams.productCode,
      productContractAbbreviation: this.queryParams.productContractAbbreviation
    };
    if (params.processTags.indexOf('全部') !== -1) {
      const index = params.processTags.indexOf('全部');
      params.processTags.splice(index, 1);
    }
    this.tableLoading = true;
    queryHistoryTaskList(params).then((res) => {
      this.tableLoading = false;
      if (res.data) {
        this.tableDataList = res.data.rows;
        this.tableTotal = res.data.total;
      } else {
        this.$message.error('历史任务查询失败：' + res.error.message);
      }
    });
  }
  // 改变页码
  handleCurrent(el: any) {
    this.queryParams.page = el;
    this.getHistoryTaskList();
  }
  // 改变页大小
  handleSize(el: any) {
    this.queryParams.limit = el;
    this.queryParams.page = 1;
    this.getHistoryTaskList();
  }
  // 搜索
  handleSearch() {
    this.queryParams.page = 1;
    this.getHistoryTaskList();
  }
  // 重置
  handleReset() {
    this.queryParams.processName = [];
    this.queryParams.processNode = [];
    this.queryParams.productCode = '';
    this.queryParams.productContractAbbreviation = '';
    this.queryParams.processTags = [];
    this.queryParams.limit = 20;
    this.queryParams.page = 1;
  }
  // 查看流转历史
  flowHistory(record) {
    this.processInstanceId = record.processInstanceId;
    // 弹出任务办理状态流程流程图
    this.histroyDialog = true;
  }
  // 关闭流转历史弹出
  cancelHistroyDialog() {
    this.histroyDialog = false;
  }
}
